.GitHub-status .GitHub-icon {
  display: inline-block;
  margin-right: .5em;
}

.GitHub-issue {
  font-size: .8em;
  padding: .5em;
  background: rgba(255,255,255,0.05);
  margin-bottom: .25em;
  text-align: left;
}

.GitHub-issue .title {
  font-weight: bold;
}
.GitHub-issue .title a {
  color: inherit;
  text-decoration: none;
}
.GitHub-issue .info {
  opacity: 0.6;
  display: grid;
  grid-template-columns: 3fr 1fr;
}

.GitHub-issue .repo {
  display: inline-block;
  margin-right: .5em;
}

.GitHub-issue .author {
  display: inline-block;
  font-weight: bold;
  font-style: italic;
  text-align: right;
}