.App {
  background: rgba(0,0,0,0.7);
  min-height: 100vh;
}

.App > .bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-position-x: center;
  background-position-y: bottom;
  background-size: cover;
  z-index: -1;
}

.App-header {
  padding-top: .75em;
  text-transform: uppercase;
  letter-spacing: 0.2em;
}
.App-header h1 {
  margin: 0;
  font-size: 2.5em;
}
.App .App-header .today > div {
  display: inline-block;
}
.App .App-header .today .date {
  margin-right: 0.75em;
}
.App-header .date {
  display: inline-block;
}
.App-header .time {
  font-weight: bold;
  display: inline-block;
}

.App.landscapelarge .App-header {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  padding: 1em 1.5em 0 1.5em;
  margin-bottom: 0.5em;
}
.App.landscapelarge .App-header .today {
  text-align: right;
}
.App.landscapelarge .App-header .today > div {
  display: block;
}
.App.landscapelarge .App-header .time {
  font-size: 90%;
}

.App.portraitsmall {
  font-size: 1em;
  padding: .5em .75em;
  background: #0c2735;
}
.App.portraitsmall .bg {
  display: none;
}