@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.1em;
  color: white;
  text-align: center;
}

h1, h2, h3, h4, h5 {
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: .2em;
}

#root > .error {
  color: red;
  text-align: center;
  margin-top: 5em;
}
.App {
  background: rgba(0,0,0,0.7);
  min-height: 100vh;
}

.App > .bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-position-x: center;
  background-position-y: bottom;
  background-size: cover;
  z-index: -1;
}

.App-header {
  padding-top: .75em;
  text-transform: uppercase;
  letter-spacing: 0.2em;
}
.App-header h1 {
  margin: 0;
  font-size: 2.5em;
}
.App .App-header .today > div {
  display: inline-block;
}
.App .App-header .today .date {
  margin-right: 0.75em;
}
.App-header .date {
  display: inline-block;
}
.App-header .time {
  font-weight: bold;
  display: inline-block;
}

.App.landscapelarge .App-header {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  padding: 1em 1.5em 0 1.5em;
  margin-bottom: 0.5em;
}
.App.landscapelarge .App-header .today {
  text-align: right;
}
.App.landscapelarge .App-header .today > div {
  display: block;
}
.App.landscapelarge .App-header .time {
  font-size: 90%;
}

.App.portraitsmall {
  font-size: 1em;
  padding: .5em .75em;
  background: #0c2735;
}
.App.portraitsmall .bg {
  display: none;
}
.widgets {
  padding: 1em 1.5em;
}

.widgets div[class^="widget-row-"], div[class*=" widget-row-"] {
  display: grid;
  grid-gap: .5em;
}
.widgets .widget-row-2 { grid-template-columns: 1fr 1fr; }
.widgets .widget-row-3 { grid-template-columns: 1fr 1fr 1fr; }
.widgets .widget-row-4 { grid-template-columns: 1fr 1fr 1fr 1fr; }
.widgets .widget-row-5 { grid-template-columns: 1fr 1fr 1fr 1fr 1fr; }

.Widget {
  margin-bottom: 1.25em;
}
.Widget .Widget-header h1 {
  font-size: 1.25em;
  margin-top: 0;
}
.Events-header h2 {
  margin-top: 0;
  margin-bottom: 1em;
}
.Event {
  display: grid;
  grid-template-columns: 2fr 5fr;
  grid-gap: 0;
  margin-bottom: .25em;
  text-align: left;
}
.Event:last-child {
  margin-bottom: 0;
}
.Event > span {
  display: inline-block;
}
.Event > span:last-child {
  margin-right: 0;
}
.Event .date {
  text-align: center;
  background-color: rgba(0,0,0,0.5);
  padding: 0.5em
}
.Event .month {
  font-weight: bold;
  margin-right: 0.4em;
}
.Event .day {
  font-weight: 300;
}
.Event .info {
  background-color: rgba(160, 160, 160, 0.5);
  padding: .5em;
}
.Event .time {
  font-weight: 300;
  margin-right: 0.5em;
}
.Widget.football .Fixture  {
  background: rgba(255,255,255,0.3);
  padding: .75em;
  margin-bottom: .25em;
}
.Widget.football .Fixture .teams {
  display: grid;
  grid-template-columns: 3fr 2fr 3fr;
}
.Widget.football .Fixture .teams .logo img {
  max-height: 50px;
}
.Widget.football .Fixture .teams .info {
  display: table;
}
.Widget.football .Fixture .teams .info .inner {
  display: table-cell;
  vertical-align: middle;
}
.Widget.football .Fixture .teams .info .inner .score {
  font-weight: bold;
  font-size: 1.5em;
}
.Widget.football .Fixture .teams .info .inner .venue {
  font-size: .75em;
}
.GitHub-status .GitHub-icon {
  display: inline-block;
  margin-right: .5em;
}

.GitHub-issue {
  font-size: .8em;
  padding: .5em;
  background: rgba(255,255,255,0.05);
  margin-bottom: .25em;
  text-align: left;
}

.GitHub-issue .title {
  font-weight: bold;
}
.GitHub-issue .title a {
  color: inherit;
  text-decoration: none;
}
.GitHub-issue .info {
  opacity: 0.6;
  display: grid;
  grid-template-columns: 3fr 1fr;
}

.GitHub-issue .repo {
  display: inline-block;
  margin-right: .5em;
}

.GitHub-issue .author {
  display: inline-block;
  font-weight: bold;
  font-style: italic;
  text-align: right;
}
.MealIcon {
  padding: 0.5em;
}
.MealIcon img {
  max-width: 50px;
}

.Meals-meals {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-gap: .15em;
}
.Meal {
  background-color: hsla(0,0%,62.7%,.5);
  padding: 0.25em;
}
.Meal.today {
  border: 2px solid white;
}
.Meal .day {
  padding-bottom: 0.25em;
  background: rgba(0,0,0,0.7);
}
.Meal .name {
  font-weight: bold;
}
.Meal .owner {
  opacity: 0.5;
}

.List-empty {
  display: inline-block;
  margin-top: .5em;
  font-size: 130%;
  font-style: italic;
}

.List-items > .ListItem.compact .info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: .25em;
}

.ListItem {
  font-weight: normal;
  margin-bottom: 0.25em;
  background-color: rgba(160, 160, 160, 0.5);
  padding: 0.5em;
  text-align: left;
}
.ListItem:last-child {
  margin-bottom: 0;
}

.ListItem.compact {
  background: red;
}

.Widget.news .ListItem > div {
  display: grid;
  grid-template-columns: 9fr 2fr 1fr;
  grid-gap: .25em;
}
.Widget.news .ListItem .source {
  font-weight: bold;
  text-align: right;
}
.Widget.news .ListItem .date {
  font-size: 90%;
  text-align: right;
  margin-right: 0.25em;
  font-weight: normal;
}
.Widget.technews .List-items > .ListItem {
  padding: 0;
}
.Widget.technews .List-items > .ListItem .info {
  /* display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: .25em; */
}
.Widget.technews .ListItem .source {
  /* font-weight: bold;
  text-align: right; */
}
.Widget.technews .ListItem .date {
  /* font-size: 90%;
  text-align: right;
  margin-right: 0.25em;
  font-weight: normal; */
}
.Weather-weather {
  display: flex;
}
.Weather-weather .Weather-summary {
  flex: auto;
}
.Weather-weather .Weather-summary .inner {
  margin: 0.15em;
  padding: 0.5em;
  text-align: center;
}
.Weather-weather .Weather-summary .summary {
  font-weight: bold;
}
