.Meals-meals {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-gap: .15em;
}
.Meal {
  background-color: hsla(0,0%,62.7%,.5);
  padding: 0.25em;
}
.Meal.today {
  border: 2px solid white;
}
.Meal .day {
  padding-bottom: 0.25em;
  background: rgba(0,0,0,0.7);
}
.Meal .name {
  font-weight: bold;
}
.Meal .owner {
  opacity: 0.5;
}
