.Widget.football .Fixture  {
  background: rgba(255,255,255,0.3);
  padding: .75em;
  margin-bottom: .25em;
}
.Widget.football .Fixture .teams {
  display: grid;
  grid-template-columns: 3fr 2fr 3fr;
}
.Widget.football .Fixture .teams .logo img {
  max-height: 50px;
}
.Widget.football .Fixture .teams .info {
  display: table;
}
.Widget.football .Fixture .teams .info .inner {
  display: table-cell;
  vertical-align: middle;
}
.Widget.football .Fixture .teams .info .inner .score {
  font-weight: bold;
  font-size: 1.5em;
}
.Widget.football .Fixture .teams .info .inner .venue {
  font-size: .75em;
}