.List-empty {
  display: inline-block;
  margin-top: .5em;
  font-size: 130%;
  font-style: italic;
}

.List-items > .ListItem.compact .info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: .25em;
}

.ListItem {
  font-weight: normal;
  margin-bottom: 0.25em;
  background-color: rgba(160, 160, 160, 0.5);
  padding: 0.5em;
  text-align: left;
}
.ListItem:last-child {
  margin-bottom: 0;
}

.ListItem.compact {
  background: red;
}
