.Widget.news .ListItem > div {
  display: grid;
  grid-template-columns: 9fr 2fr 1fr;
  grid-gap: .25em;
}
.Widget.news .ListItem .source {
  font-weight: bold;
  text-align: right;
}
.Widget.news .ListItem .date {
  font-size: 90%;
  text-align: right;
  margin-right: 0.25em;
  font-weight: normal;
}