.widgets {
  padding: 1em 1.5em;
}

.widgets div[class^="widget-row-"], div[class*=" widget-row-"] {
  display: grid;
  grid-gap: .5em;
}
.widgets .widget-row-2 { grid-template-columns: 1fr 1fr; }
.widgets .widget-row-3 { grid-template-columns: 1fr 1fr 1fr; }
.widgets .widget-row-4 { grid-template-columns: 1fr 1fr 1fr 1fr; }
.widgets .widget-row-5 { grid-template-columns: 1fr 1fr 1fr 1fr 1fr; }

.Widget {
  margin-bottom: 1.25em;
}
.Widget .Widget-header h1 {
  font-size: 1.25em;
  margin-top: 0;
}