.Events-header h2 {
  margin-top: 0;
  margin-bottom: 1em;
}
.Event {
  display: grid;
  grid-template-columns: 2fr 5fr;
  grid-gap: 0;
  margin-bottom: .25em;
  text-align: left;
}
.Event:last-child {
  margin-bottom: 0;
}
.Event > span {
  display: inline-block;
}
.Event > span:last-child {
  margin-right: 0;
}
.Event .date {
  text-align: center;
  background-color: rgba(0,0,0,0.5);
  padding: 0.5em
}
.Event .month {
  font-weight: bold;
  margin-right: 0.4em;
}
.Event .day {
  font-weight: 300;
}
.Event .info {
  background-color: rgba(160, 160, 160, 0.5);
  padding: .5em;
}
.Event .time {
  font-weight: 300;
  margin-right: 0.5em;
}